.network{
    display: flex;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
    margin-bottom: 30px;
    padding-left: 1rem;
    padding-right: 1rem;
}
.boton{
    flex: 0 0 50%;
    max-width: 50%;
    position: relative;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
}

.facebook{
    background-color: #3b5998;
    color: #FFFFFF;
    padding-left: 50px;
    overflow: hidden;
    position: relative;
    padding: 12px 20px;
    font-size: 12px;
    display: block;
    font-weight: 900;
    border-radius: 8px;
    text-transform: uppercase;
    text-align: center;
    cursor: pointer;
    user-select: none;
}

.facebook i{
    position: absolute;
    left: 0;
    top: 0;
    line-height: 43px;
    width: 40px;
    height: 100%;
    background-color: rgba(0,0,0,.1);
}

