.second{
    background-image: url('/build/img/14m.webp');
}

.third{
    background-image: url('/build/img/17m.webp');
}

.first{
    background-image: url('/build/img/8m.webp');
}
.card{
    background-size: cover;
    background-position: center center;
    height: 320px;
    position: relative;
    margin-left: auto;
    margin-right: auto;
    display:none;
}

.img{
    overflow: hidden;
    border-radius: 3rem;
    margin: 1rem;
}

.bg-card{
    background: linear-gradient(to bottom,rgba(240,240,240,0) 0%,rgba(240,240,240,0) 30%,rgba(240,240,240,0.3) 40%,rgba(240,240,240,0.5) 50%,#f0f0f0 80%,rgba(247,247,247,0.95) 100%);
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    position: absolute;
    z-index: 1;
    background-size: cover;
}
.content-card{
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 10;
}
.content-card h1{
    font-size: 24px !important;
    font-weight: $bold;
    margin: 0;
    margin-bottom: 5px;
}
.content-card p{
    width: 90%;
    max-width: 340px;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: 0;
    margin-bottom: 30px;
    word-wrap: break-word;
    font-size: 13px;
}
.carrousel{
    position:relative;
    margin:auto;
}

.fade{
    -webkit-animation-name:fade;
    -webkit-animation-duration:1.5s;
    animation-name:fade;
    animation-duration:1.5s;
   }
   
   @-webkit-keyframes fade{
    from{opacity:.4}
    to{opacity:1}
   }
   
   @keyframes fade{
    from{opacity:.4}
    to{opacity:1}
   }

.header-bottom{
    margin-top: -20rem;
    z-index: 10;
}