html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-size: 1.6rem;
    line-height: 1.8;
    min-height: 550px;
    background-color: #f0f0f0;
    overflow-x: hidden;
}

.contenedor {
    width: 95%;
    max-width: 160rem;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

img,
picture {
    width: 95%;
}

h1, h2, h3 {
    text-align: center;
    font-family: $fuente_secundaria;
    font-size: 24px;
}
p{
    font-family: $fuente_principal;
}