.background{
    background-image: url('/build/img/20.webp');
    background-size: cover;
    background-position: center center;
    position: relative;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 310px;
    background-size: cover;
    z-index: 0;
    border-bottom-left-radius: 45px;
    border-bottom-right-radius: 45px;
}

.mask{
    background-color: #358AE7;
    background-position: center center;
    background-size: cover;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    position: absolute;
    z-index: 1;
    opacity: .9;
    border-bottom-left-radius: 45px;
    border-bottom-right-radius: 45px;
}

.logo{
    width: 200px;
    margin: 50px auto 50px auto;
    display: table;
}