.card-gallery{
    border-radius: 15px;
    overflow: hidden;
    margin-bottom: 1rem;
    width: 100%;
    height: 30rem;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
}
.gallery{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-left: 2rem;
    padding-right: 1rem;
}
.mask-gallery{
    padding: 1rem;
    height: 100%;
    position: relative;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(to bottom,rgba(0,0,0,0) 0%,rgba(0,0,0,0) 20%,rgba(0,0,0,0.1) 30%,rgba(0,0,0,0.8) 70%,#000 100%);
}

.section-service{
    @include grid(2, 1rem);
    width: 100%;
    justify-content: center;
}

.card-gallery-grid{
    border-radius: 15px;
    overflow: hidden;
    margin-bottom: 1rem;
    width: 100%;
    height: 20rem;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
}