.active {
  background-color: #0034c8;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  border: none;
  border-radius: 5px;
}
.active:hover {
  background-color: #002ba4;
  cursor: pointer;
}

.inactive {
  background-color: #616161;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  border: none;
  border-radius: 5px;
}
.inactive:hover {
  background-color: #4f4f4f;
  cursor: pointer;
}

.boton-azul-block {
  background-color: #0034c8;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
  border-radius: 5px;
}
.boton-azul-block:hover {
  background-color: #002ba4;
  cursor: pointer;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
  min-height: 550px;
  background-color: #f0f0f0;
  overflow-x: hidden;
}

.contenedor {
  width: 95%;
  max-width: 160rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 95%;
}

h1, h2, h3 {
  text-align: center;
  font-family: "Poppins", sans-serif;
  font-size: 24px;
}

p {
  font-family: "Roboto", sans-serif;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.background {
  background-image: url("/build/img/20.webp");
  background-size: cover;
  background-position: center center;
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 310px;
  background-size: cover;
  z-index: 0;
  border-bottom-left-radius: 45px;
  border-bottom-right-radius: 45px;
}

.mask {
  background-color: #358AE7;
  background-position: center center;
  background-size: cover;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  position: absolute;
  z-index: 1;
  opacity: 0.9;
  border-bottom-left-radius: 45px;
  border-bottom-right-radius: 45px;
}

.logo {
  width: 200px;
  margin: 50px auto 50px auto;
  display: table;
}

.second {
  background-image: url("/build/img/14m.webp");
}

.third {
  background-image: url("/build/img/17m.webp");
}

.first {
  background-image: url("/build/img/8m.webp");
}

.card {
  background-size: cover;
  background-position: center center;
  height: 320px;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  display: none;
}

.img {
  overflow: hidden;
  border-radius: 3rem;
  margin: 1rem;
}

.bg-card {
  background: linear-gradient(to bottom, rgba(240, 240, 240, 0) 0%, rgba(240, 240, 240, 0) 30%, rgba(240, 240, 240, 0.3) 40%, rgba(240, 240, 240, 0.5) 50%, #f0f0f0 80%, rgba(247, 247, 247, 0.95) 100%);
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  position: absolute;
  z-index: 1;
  background-size: cover;
}

.content-card {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 10;
}

.content-card h1 {
  font-size: 24px !important;
  font-weight: 700;
  margin: 0;
  margin-bottom: 5px;
}

.content-card p {
  width: 90%;
  max-width: 340px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  margin-top: 0;
  margin-bottom: 30px;
  word-wrap: break-word;
  font-size: 13px;
}

.carrousel {
  position: relative;
  margin: auto;
}

.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 1.5s;
}

@-webkit-keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}
@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}
.header-bottom {
  margin-top: -20rem;
  z-index: 10;
}

.network {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
  margin-bottom: 30px;
  padding-left: 1rem;
  padding-right: 1rem;
}

.boton {
  flex: 0 0 50%;
  max-width: 50%;
  position: relative;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
}

.facebook {
  background-color: #3b5998;
  color: #FFFFFF;
  padding-left: 50px;
  overflow: hidden;
  position: relative;
  padding: 12px 20px;
  font-size: 12px;
  display: block;
  font-weight: 900;
  border-radius: 8px;
  text-transform: uppercase;
  text-align: center;
  cursor: pointer;
  user-select: none;
}

.facebook i {
  position: absolute;
  left: 0;
  top: 0;
  line-height: 43px;
  width: 40px;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.1);
}

.whatsapp {
  background-color: #34AF23;
  color: #FFFFFF;
  padding-left: 50px;
  overflow: hidden;
  position: relative;
  padding: 12px 20px;
  font-size: 12px;
  display: block;
  font-weight: 900;
  border-radius: 8px;
  text-transform: uppercase;
  text-align: center;
  cursor: pointer;
  user-select: none;
}

.whatsapp i {
  position: absolute;
  left: 0;
  top: 0;
  line-height: 43px;
  width: 40px;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.1);
}

.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 98;
  background-color: rgba(249, 249, 249, 0.98);
  box-shadow: 0 -5px 10px 0 rgba(0, 0, 0, 0.06);
  min-height: 60px;
  width: 100%;
  display: flex;
  text-align: center;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  transition: all 300ms ease;
}

footer a {
  color: #1f1f1f;
  padding-top: 12px;
  position: relative;
  flex: 1 1 auto;
}

.footer a span {
  position: relative;
  z-index: 2;
  display: block;
  font-size: 10px;
  font-weight: 500;
  margin-top: -6px;
  opacity: 0.7;
  font-family: "Roboto", sans-serif;
}

.footer a i {
  color: #0034c8;
}

.service_one {
  background-image: url("/build/img/services/1.webp");
}

.service_two {
  background-image: url("/build/img/services/2.webp");
}

.service_three {
  background-image: url("/build/img/services/3.webp");
}

.service_four {
  background-image: url("/build/img/services/4.webp");
}

.service_five {
  background-image: url("/build/img/services/5.webp");
}

.service_six {
  background-image: url("/build/img/services/6.webp");
}

.service_seven {
  background-image: url("/build/img/services/7.webp");
}

.card-gallery {
  border-radius: 15px;
  overflow: hidden;
  margin-bottom: 1rem;
  width: 100%;
  height: 30rem;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.gallery {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-left: 2rem;
  padding-right: 1rem;
}

.mask-gallery {
  padding: 1rem;
  height: 100%;
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 20%, rgba(0, 0, 0, 0.1) 30%, rgba(0, 0, 0, 0.8) 70%, #000 100%);
}

.section-service {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
  width: 100%;
  justify-content: center;
}

.card-gallery-grid {
  border-radius: 15px;
  overflow: hidden;
  margin-bottom: 1rem;
  width: 100%;
  height: 20rem;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}